@keyframes opacifyAndSlideIn {
  from {
    opacity: 0;
    transform: translateX(-80px) scale(0.75);
  }

  to {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

@keyframes opacifyIn {
  from {
    opacity: 0;
    transform: scale(0.75);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@mixin text-outline($size: 1px, $color: $black) {
  text-shadow: -$size -$size 0 $color, $size -$size 0 $color, -$size $size 0 $color, $size $size 0 $color;
}

@mixin center-children($display: inline-flex) {
  display: $display;
  justify-content: center;
  align-items: center;
}

@mixin grow-on-hover() {
  transition: all 0.2s;

  &:not(:disabled) {
    &:hover {
      transform: scale(1.1);
      z-index: 1;
    }

    &:active {
      transform: scale(1.1) translate(2px, 2px);
    }
  }
}

@mixin droppable() {
  // $stripe1: transparent;
  // $stripe2: $light-gray2;
  // background: repeating-linear-gradient(45deg, $stripe1, $stripe1 10px, $stripe2 10px, $stripe2 20px);
  border: 2px solid $light-gray4;
  transition: border-color 0.2s;

  &.dragging {
    border-color: $blue3;
  }
}
